* {
  box-sizing: 0;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #161616;
  color: #f0f0f0;
  outline: none;
}

@font-face {
  src: url(fonts/Sister Spray.ttf);
  font-family: graffiti;
}

@font-face {
  src: url(fonts/Chillow-Regular.ttf);
  font-family: chillow;
}

@font-face {
  src: url(fonts/Striker.otf);
  font-family: striker;
}

.app {
  display: flex;
  flex-direction: column;
  align-self: center;
  margin: auto;
}

.titleContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
}

h1 {
  position: absolute;
  font-family: graffiti;
  font-weight: lighter;
  font-size: 90px;
  color: red;
  text-shadow: 3px -4px 1px #ff6ee0;
}

.title2 {
  color: rgba(0, 0, 0, 0);
  text-shadow: -5px -3px 1px #38f2ff;
}

.todoContainer {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-bottom: 25px;
}

.todo {
  width: 1200px;
  display: flex;
  justify-content: space-between;
}

.removeTodo {
  font-family: striker;
  background-color: red;
  border: none;
  width: 40px;
  height: 40px;
  font-size: 16px;
  border-radius: 15px;
  margin-left: 10px;
}

.removeTodo:hover{
  background-image: linear-gradient(140deg, #ff6ee0, #38f2ff);
  box-shadow: 0 0 18px red;
}

.todoTextRemoveTodoContainer {
  font-family: chillow;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  margin-right: 14px;
  border-style: dashed dashed solid solid;
  border-width: 3px;
  border-color: #ff6ee0 #38f2ff #38f2ff #ff6ee0;
  border-radius: 0 25px 25px 0;
  font-size: 23px;
  transition: background-color .2s;
}

.todoText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  text-align: center;
  word-break: break-all;
}

.completeTodoOuterContainer {
  margin-right: 120px;
}

.completeTodoContainer {
  perspective: 200px;
}

.completeTodo {
  font-family: "Arial Black", Gadget, sans-serif;
  position: absolute;
  font-size: 28px;
  padding: 7px 15px;
  border: none;
  background-color: red;
  box-shadow: -1px -4px 1px #ff6ee0;
  transform: rotateY(40deg);
  transition: box-shadow .5s;
}

.completeTodo2 {
  font-family: "Arial Black", Gadget, sans-serif;
  position: absolute;
  font-size: 28px;
  padding: 7px 18px;
  border: none;
  background-color: red;
  box-shadow: 4px 1px 1px #38f2ff;
  transform: rotateY(40deg);
  transition: box-shadow .5s;
}

.completeTodoOuterContainer:hover > .completeTodo {
  box-shadow: -4px -2px 1px #ff6ee0;
}

.completeTodoOuterContainer:hover > .completeTodo2 {
  box-shadow: 10px 3px 1px #38f2ff;
}

.completeTodoOuterContainer:hover + .todoTextRemoveTodoContainer {
  background-color: #00bac7;
}

.completeTodoOuterContainer:hover + .todoTextRemoveTodoContainer .todoText {
  background-color: #c40099;
}

button {
  margin-right: 10px;
  cursor: pointer;
}

input {
  font-size: 25px;
  width: 100%;
  max-width: 1000px;
  margin-top: 30px;
  padding: 8px;
}








@media only screen and (max-width: 768px) {
  .app {
    width: 225px;
  }

  .titleContainer {
    height: 120px;
  }

  h1 {
    font-size: 21px;
    text-shadow: 1px -1px 1px #ff6ee0;
  }

  .title2 {
    text-shadow: -2px -1px 1px #38f2ff;
  }

  .completeTodoOuterContainer {
    margin-right: 0px;
  }

  .todoContainer {
    margin-bottom: 15px;
    justify-content: space-around;
  }

  .todo {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .removeTodo {
    width: 25px;
    height: 25px;
    font-size: 10px;
    border-radius: 8px;
    margin-left: 5px;
  }
  
  .removeTodo:hover{
    background-image: linear-gradient(140deg, #ff6ee0, #38f2ff);
    box-shadow: 0 0 10px red;
  }

  .todoTextRemoveTodoContainer {
    padding: 8px;
    border-width: 2px;
    border-radius: 0 22px 22px 0;
    font-size: 13px;
    margin-right: 5px;
  }

  .completeTodoContainer {
    margin-right: 62px;
  }
  
  .completeTodo {
    font-size: 16px;
    padding: 3px 5px;
  }
  
  .completeTodo2 {
    font-size: 16px;
    padding: 3px 8px;
  }

  button {
    margin: 0px;
  }
  
  input {
    font-size: 15px;
    margin-top: 20px;
    padding: 8px;
  }
}